import { Component, Inject, OnInit } from '@angular/core';
import { ITipoMovimiento } from '../../models/tipo-movimiento';
import { TipoDeMovimientosService } from '../../service/tipo-movimientos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
@Component({
    selector: 'app-save-tipo-movimiento',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    tipo_movimiento: ITipoMovimiento;
    title: string;
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _tiposMovimientosService: TipoDeMovimientosService,
        public alert: AppAlertService,
        public menu: JedsMenuService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.tipo_movimiento = this.data ? this.data : null;
        this.title = this.tipo_movimiento?.idtipo ? 'Editar tipo de Movimiento' : 'Nuevo tipo de Movimiento';
    }

    ngOnInit(): void {
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            nombre: new FormControl((this.tipo_movimiento ? this.tipo_movimiento.descripcion : ''), Validators.required),
            activo: new FormControl((this.tipo_movimiento ? (this.tipo_movimiento.activo ? 1 : 0) : 1), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.tipo_movimiento = {
                ...this.tipo_movimiento,
                descripcion: this.form.get('nombre').value,
                activo: this.form.get('activo').value,
            }
            if (this.tipo_movimiento.idtipo) {
                this._tiposMovimientosService.updateTipoMovimiento(this.tipo_movimiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this._tiposMovimientosService.saveTipoMovimiento(this.tipo_movimiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu();
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}